.style-homePage {
    text-align: center;

}

p {
    color: rgb(255, 255, 255);
    font-size: calc(12px + 2vmin);
}
/*
    Logo
*/
.style-logo {


  padding: 2rem;






  /* IMPORTANTE */
  text-align: center;
  padding: 10;

}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      -webkit-animation: App-logo-spin infinite 30s linear;
              animation: App-logo-spin infinite 30s linear;
    }
  }
  
  .App-header {
    background-color: #32183a;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 1vmin);
    color: white;
  }
  


  @-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  


  @keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  

/* No cambiar el orden */
a {
    font-size: calc(12px + 1vmin);
    text-decoration: none;
}

a:link {    color: rgb(13, 224, 24);}
a:visited { color: rgb(190, 44, 219);}
a:focus {   color: rgb(63, 3, 87);}
a:hover {   color: rgb(213, 68, 241);}
a:active {  color: rgb(155, 35, 235);}
